
import axios from "axios";
export default {
  name: "DoctorPage",
  head() {
    return { title: `Doctors list - ${this.$pageTitle}` };
  },
  data() {
    return {
      doctors: [],
    };
  },
  async asyncData({ store, error }) {
    try {
      let res = await axios.get(`${store.getters.baseUrl}/api/fetch/doctors`);

      if (res.data) {
        const { doctors } = res.data;
        return { doctors };
      } else {
        error({ statusCode: 404, message: "Not found" });
      }
    } catch (err) {
      console.error(err);
      error({
        statusCode: 500,
        message: "Something wrong. Please try again",
      });
    }
  },
};
